.form-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #002447;
  }
  
  .register-form {
    border: 2px solid #002447;
    padding: 30px;
    background-color: #FBECEB;
    color: #002447;
    border-radius: 4%;
    width: 30%
  }

  .register-form button{
    background-color: #002447;
  }

  .register-form Link{
    color: #FEB249;
  }