.logo h6{
    font-size: 1.5rem;
    text-align: center;
    margin: 20px 0px;
}

.menu {
    margin-top: 50px;
  }
  .menu-item {
    margin-top: 30px;
  }
  .menu-item a {
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
  }
  .menu-item i {
    font-size: 1.2rem;
    margin: 0 15px;
  }
  .active {
    background-color: white;
    padding: 10px;
  }
  .active a,
  .active i {
    color: rgb(51, 1, 1);
  }

.header-content{
    display: flex;
    align-items: center;
    height : 50px;
    justify-content : flex-end;
    margin: 0 15px;
    
}

.header-content i{
    margin-right : 20px;
    font-size: 1.2rem;
}

.header-content a{
    text-decoration: none;
    font-size: 1.2rem;
    color: blue;
    margin: 0 10px;
    text-transform: uppercase;
}
